import { useTheme } from '@emotion/react';
import { Box, Container, Typography } from '@mui/material';
import Grid from '@mui/material/Unstable_Grid2';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import chair from '../../img/Product/chair.png';

export const Product = () => {
   const theme = useTheme();

   return (
      <Box component='section' sx={{ py: '100px', overflow: 'hidden' }}>
         <Container maxWidth='lg'>
            <Grid
               container
               spacing={2}
               sx={{
                  flexDirection: 'row-reverse',
                  justifyContent: 'flex-end',
                  gap: { md: 0, xs: '50px' }
               }}>
               <Grid md={6} ms={12} >
                  <Box maxWidth='550px'>
                     <Typography
                        component="p"
                        sx={{
                           lineHeight: '180%',
                           fontSize: '14px',
                           color: theme.colors.secondary,
                           background: theme.colors.secondaryS,
                           display: 'inline',
                           px: '14px',
                           py: '7px',
                        }}>
                        OUR PRODUCT STORIES
                     </Typography>
                     <Typography
                        component="h2"
                        sx={{
                           lineHeight: '130%',
                           fontWeight: '700',
                           fontSize: { sm: '48px', xs: '40px' },
                           mt: '10px',
                           mb: '35px',
                        }}>
                        Why product stories? Learn more
                     </Typography>
                     <Box>
                        <Typography
                           component="p"
                           sx={{
                              display: 'flex',
                              gap: '15px',
                              alignItems: 'center',
                              mb: '21px',
                           }}>
                           <CheckCircleIcon
                              sx={{
                                 fontSize: '37px',
                                 color: theme.colors.secondary,
                              }} />
                           SEO optimized, high Google work
                        </Typography >
                        <Typography
                           component="p"
                           sx={{
                              mb: '50px',
                              color: theme.colors.whiteH,
                              maxWidth: '460px',
                           }}>
                           Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                        </Typography >
                        <Typography
                           component="p"
                           sx={{
                              display: 'flex',
                              gap: '15px',
                              alignItems: 'center',
                              mb: '21px',

                           }}>
                           <CheckCircleIcon
                              sx={{
                                 fontSize: '37px',
                                 color: theme.colors.secondary
                              }} />
                           Fast loading, low bounce rates
                        </Typography >
                        <Typography
                           component="p"
                           sx={{
                              color: theme.colors.whiteH,
                              maxWidth: '460px',
                           }}>
                           It is a long established fact that a reader will be distracted by the readable content
                        </Typography >
                     </Box>
                  </Box>
               </Grid>
               <Grid md={6} ms={12} sx={{ flexGrow: 1 }}>
                  <Box
                     color='#fff'
                     sx={{
                        textAlign: 'center',
                        mx: 'auto',
                        position: 'relative',
                        height: { lg: '700px', xs: '100%' },
                     }}>
                     <Box component='img'
                        sx={{
                           position: { lg: 'absolute', md: 'static' },
                           left: { lg: '-120px' },
                           zIndex: { lg: '-1' },
                           width: { lg: '680px', xs: '100%' },
                           maxWidth: { lg: 'none', xs: '500px' },
                        }}
                        src={chair}
                        alt="man" />
                  </Box>
               </Grid>
            </Grid>
         </Container>
      </Box >
   )
}