import { useTheme } from '@emotion/react';
import { Box, Typography } from '@mui/material';
import Grid from '@mui/material/Unstable_Grid2';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import imgBg from '../../img/Satisfied/man.png'
import { Btn2 } from 'components/Header/Header.styled';
import { ContainerCust, Inp } from './Satified.styled';

export const Satisfied = () => {
   const theme = useTheme();

   return (
      <Box component='section' sx={{ pt: { lg: '0px', xs: '100px' }, pb: '150px', overflow: 'hidden' }}>
         <ContainerCust maxWidth='lg'>
            <Grid container spacing={2} sx={{ alignItems: { lg: 'center', xs: 'flex-start' } }}>
               <Grid md={6} ms={12} >
                  <Box maxWidth='550px'>
                     <Typography
                        component="p"
                        sx={{
                           lineHeight: '180%',
                           fontSize: '14px',
                           color: theme.colors.secondary,
                           background: theme.colors.secondaryS,
                           display: 'inline',
                           px: '14px',
                           py: '7px',
                        }}>
                        100% SATISFIED GUARANTEE
                     </Typography>
                     <Typography
                        component="h1"
                        sx={{
                           lineHeight: '130%',
                           fontWeight: '700',
                           fontSize: { sm: '62px', xs: '40px' },
                           mt: '10px',
                           mb: '35px',
                        }}>
                        Power up your
                        shopify products
                     </Typography>
                     <Typography
                        component="p"
                        sx={{
                           mb: '50px',
                           color: theme.colors.whiteH,
                           maxWidth: '460px',
                        }}>
                        More than 90,000+ companies trust our business. Get help from a dedicated shopify developer today.
                     </Typography >
                     <Box
                        component='form'
                        maxWidth='485px'
                        sx={{
                           display: 'flex',
                           position: 'relative',
                           mb: '35px'
                        }}
                        action="#">
                        <MailOutlineIcon
                           sx={{
                              position: 'absolute',
                              top: '16px',
                              left: '22px',
                              color: theme.colors.whiteH,
                              fontSize: '31px'
                           }} />
                        <Inp placeholder='Your email address' type="text" />
                        <Btn2 variant="contained"
                           sx={{
                              borderRadius: '10px',
                              position: 'absolute',
                              top: '10px',
                              right: '8px',
                              background: theme.colors.primary,
                              color: theme.colors.background,
                              py: '10px',
                              px: { sm: '32px', xs: '10px' },
                              fontWeight: '600',
                              textTransform: 'none'
                           }}>
                           Sign up
                        </Btn2>
                     </Box>
                     <Box
                        sx={{
                           display: 'flex',
                           columnGap: '48px',
                           rowGap: '20px',
                           flexWrap: 'wrap',
                           justifyContent: { sm: 'flex-start', xs: 'center' }
                        }}>
                        <Typography
                           component="p"
                           sx={{
                              display: 'flex',
                              gap: '15px',
                              alignItems: 'center',
                           }}>
                           <CheckCircleIcon
                              sx={{
                                 fontSize: '37px',
                                 color: theme.colors.secondary,
                              }} />
                           30 days free trial
                        </Typography >
                        <Typography
                           component="p"
                           sx={{
                              display: 'flex',
                              gap: '15px',
                              alignItems: 'center',

                           }}>
                           <CheckCircleIcon
                              sx={{
                                 fontSize: '37px',
                                 color: theme.colors.secondary
                              }} />
                           No credit card required
                        </Typography >
                     </Box>
                  </Box>
               </Grid>
               <Grid md={6} ms={12} sx={{ flexGrow: 1 }}>
                  <Box
                     color='#fff'
                     sx={{
                        textAlign: 'center',
                        mx: 'auto',
                        position: 'relative',
                        height: { lg: '1000px', xs: '100%' },
                     }}>
                     <Box component='img'
                        sx={{
                           position: { lg: 'absolute', md: 'static' },
                           left: { lg: '-120px' },
                           pt: { lg: '126px' },
                           zIndex: { lg: '-1' },
                           width: { lg: '780px', xs: '100%' },
                           maxWidth: { lg: 'none', xs: '500px' },
                        }}
                        src={imgBg}
                        alt="man" />
                  </Box>
               </Grid>
            </Grid>
         </ContainerCust>
      </Box >
   )
}