import { useTheme } from "@emotion/react";
import { Box, Container, Typography } from "@mui/material";
import { Inp } from "./Contact.styled";
import { Btn2 } from "components/Header/Header.styled";


export const Contact = () => {
   const theme = useTheme();
   return (
      <Box component='section' sx={{ py: '100px' }}>
         <Container
            maxWidth='lg'
            sx={{
               display: 'flex',
               justifyContent: 'center'
            }}>
            <Box>
               <Typography
                  component="h2"
                  sx={{
                     lineHeight: '130%',
                     fontWeight: '700',
                     fontSize: { sm: '48px', xs: '40px' },
                     mt: '10px',
                     mb: '35px',
                  }}>
                  Contact with us.
               </Typography>
               <Typography
                  component="p"
                  sx={{
                     mb: '50px',
                     color: theme.colors.whiteH,
                     maxWidth: '460px',
                  }}>
                  More than 50,000+ companies trusted our business
               </Typography >
               <Box
                  component='form'
                  maxWidth='485px'
                  sx={{
                     display: 'flex',
                     position: 'relative',
                     mb: '35px'
                  }}
                  action="#">
                  <Inp placeholder='Your email address' type="text" />
                  <Btn2
                     variant="contained"
                     sx={{
                        borderRadius: '10px',
                        position: 'absolute',
                        top: '10px',
                        right: '8px',
                        background: theme.colors.primary,
                        color: theme.colors.background,
                        py: '10px',
                        px: { sm: '32px', xs: '10px' },
                        fontWeight: '600',
                        textTransform: 'none'
                     }}>
                     Sign up
                  </Btn2>
               </Box>
            </Box>
         </Container>
      </Box>
   )
}