import styled from '@emotion/styled';
import { Card } from '@mui/material';
import icon1 from '../../img/WhatWeDo/icon1.svg';
import icon2 from '../../img/WhatWeDo/icon2.svg';
import icon3 from '../../img/WhatWeDo/icon3.svg';
import icon4 from '../../img/WhatWeDo/icon4.svg';

export const Card1 = styled(Card)`
position: relative;
:before{
   content:'';
   background: url(${icon1}) center no-repeat, ${p => p.theme.colors.primaryH};
   position: absolute;
   width: 73px;
   height:66px; 
  top: 24px;
  left: 22px;
  border-radius: 10px;
}
`
