import { Box, Container, Typography } from "@mui/material"
import logo from 'img/logo.svg'
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import TwitterIcon from '@mui/icons-material/Twitter';
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import { useTheme } from "@emotion/react";
import { Btn3 } from '../Header/Header.styled';

const pages = ['About', 'Product', 'Service', 'Contact'];

const icons = [
   <FacebookIcon sx={{ fontSize: '33px' }} />,
   <TwitterIcon sx={{ fontSize: '33px' }} />,
   <InstagramIcon sx={{ fontSize: '33px' }} />,
   <LinkedInIcon sx={{ fontSize: '33px' }} />
];

export const Footer = () => {
   const theme = useTheme();

   return (
      <Box component='footer' sx={{ py: '100px' }}>
         <Container
            maxWidth='lg'
            sx={{
               display: 'flex',
               alignItems: 'center',
               justifyContent: {
                  md: 'space-between',
                  xs: 'space-around'
               },
               gap: '30px',
               flexWrap: 'wrap'
            }}>
            <Typography
               component="a"
               href="/"
            >
               <img src={logo} alt="logo" />
            </Typography>
            <Box
               sx={{
                  display: 'flex',
                  justifyContent: 'center',
                  flexWrap: 'wrap',
                  columnGap: '53px',
                  rowGap: '30px'
               }}>
               {pages.map((page) => (
                  <Btn3
                     key={page}
                     sx={{
                        p: '0',
                        color: theme.colors.white,
                        display: 'block',
                        fontWeight: 'regular',
                        fontSize: '14px',
                     }}
                  >
                     {page}
                  </Btn3>
               ))}
            </Box>
            <Box
               sx={{
                  display: 'flex',
               }}>
               {icons.map(icon => (
                  <Btn3
                     key={icon._source.lineNumber}
                     sx={{
                        p: '0',
                        color: theme.colors.white,
                     }}
                  >
                     {icon}
                  </Btn3>
               ))}
            </Box>
         </Container >
      </Box >
   )
}