import styled from '@emotion/styled';

export const Inp = styled.input`
background: none;
border: 1px solid #626276;
border-radius: 18px;
padding: 23px 140px 23px 22px;
display: flex;
width: 100%;
color: ${p => p.theme.colors.white};

@media (max-width: 600px){
   padding: 23px 90px 23px 22px;
}
`