import { Box } from "@mui/system"
import Grid from '@mui/material/Unstable_Grid2';
import { Container, Typography } from "@mui/material";
import { useTheme } from "@emotion/react";
import Divider from '@mui/material/Divider';
import Stack from '@mui/material/Stack';
import phone from '../../img/Services/phone.svg';

export const Services = () => {
   const theme = useTheme();

   return (
      <Box component='section' sx={{ pt: '50px', pb: '100px' }}>
         <Container maxWidth='lg'>
            <Grid
               container
               spacing={2}
               sx={{
                  alignItems: 'center',
                  flexDirection: { md: 'row', xs: 'column-reverse' }
               }}>
               <Grid md={6} xs={12}>
                  <Box sx={{ maxWidth: { md: 'none', sm: '500px', }, mx: 'auto' }}>
                     <Box component='img' sx={{ maxWidth: '100%', }} src={phone} alt="phone" />
                  </Box>
               </Grid>
               <Grid md={6} xs={12}>
                  <Box>
                     <Typography
                        component="p"
                        sx={{
                           lineHeight: '180%',
                           fontSize: '14px',
                           color: theme.colors.secondary,
                           background: theme.colors.secondaryS,
                           display: 'inline',
                           px: '14px',
                           py: '7px',
                        }}>
                        SERVICES WE PROVIDES
                     </Typography>
                     <Typography
                        component="h2"
                        sx={{
                           lineHeight: '130%',
                           fontWeight: '700',
                           fontSize: { sm: '48px', xs: '40px' },
                           mt: '10px',
                           mb: '35px',
                        }}>
                        Build on both: Google web stories and AMP website format
                     </Typography>
                     <Stack
                        direction="row"
                        divider={<Divider orientation="vertical" flexItem color={theme.colors.whiteH} />}
                        spacing={5}
                     >
                        <Box>
                           <Typography
                              component="p"
                              sx={{
                                 lineHeight: '130%',
                                 fontWeight: '700',
                                 fontSize: '38px',
                                 mb: '13px',
                                 color: theme.colors.primary,
                              }}>
                              5660
                           </Typography>
                           <Typography
                              component="p"
                              sx={{
                                 maxWidth: '220px',
                                 lineHeight: '180%',
                                 fontSize: '14px',
                                 color: theme.colors.whiteH,
                              }}>
                              Product sale per day working and sales
                           </Typography>
                        </Box>
                        <Box>
                           <Typography
                              component="p"
                              sx={{
                                 lineHeight: '130%',
                                 fontWeight: '700',
                                 fontSize: '38px',
                                 mb: '13px',
                                 color: theme.colors.accent2,
                              }}>
                              4M+
                           </Typography>
                           <Typography
                              component="p"
                              sx={{
                                 maxWidth: '220px',
                                 lineHeight: '180%',
                                 fontSize: '14px',
                                 color: theme.colors.whiteH,
                              }}>
                              Downloaded saled in your good.
                           </Typography>
                        </Box>
                     </Stack>
                  </Box>
               </Grid>
            </Grid>
         </Container>
      </Box >
   )
}