import styled from '@emotion/styled';
import { Container } from '@mui/material';


export const Inp = styled.input`
background: none;
border: 1px solid #626276;
border-radius: 18px;
padding: 23px 140px 23px 64px;
display: flex;
width: 100%;
color: ${p => p.theme.colors.white};

@media (max-width: 600px){
   padding: 23px 90px 23px 64px;
}
`
export const ContainerCust = styled(Container)`
position: relative;
:before{
   content: '';
   position: absolute;
   bottom: 60px;
   right: 0;
   width: 237px;
   height: 237px;
   border-radius: 100%;
   background: rgba(255, 190, 11, 0.5);
filter: blur(49px);
}
:after{
   content: '';
   position: absolute;
   top: 190px;
   right: -170px;
   width: 118px;
   height: 118px;
   border-radius: 100%;
   background: #8338EC;
filter: blur(49px);
}
@media (max-width:1200px){
   :before,
   :after{
      display: none;
   }
}
`